<template lang="pug">
  portal-target#app(name="semantic-ui-vue")
    .ui.fixed.top.labeled.icon.menu.fat-only
      router-link.item(to='/')
        i.home.icon
        | 首頁
      a.item(@click="$router.go(-1)" v-if = "$router.currentRoute.path != '/'")
        i.left.arrow.icon
        | 回上一頁
      router-link.item(to='/star')
        i.star.icon
        | 珍藏
      router-link.item(to='/intro')
        i.question.icon
        | 介紹
      sui-dropdown.item(icon="save outline")
        | 工具
        sui-dropdown-menu
          sui-dropdown-item(v-show="!iOS()", @click="$router.push('/myapp')")
            | 手機App
          sui-dropdown-item(@click="$router.push('/extension')")
            | 瀏覽器插件
      .right.menu
        router-link.item(to = "/chat")
          i.chat.icon
          | 留言板
        a.item(href="https://github.com/g0v/goban", target="_blank")
          i.github.icon
          | 原始碼
        .item
          iframe(src="https://www.facebook.com/plugins/share_button.php?href=http%3A%2F%2Fgoban.tw&layout=button_count&size=small&appId=485195848253155&width=71&height=20" width="71" height="20" style="border:none;overflow:hidden" scrolling="no" frameborder="0" allowTransparency="true" allow="encrypted-media")
    .ui.fixed.top.menu.thin-only
      router-link.item(to='/')
        sui-icon(name='home', data-content="首頁", title="首頁")
      a.item(@click="$router.go(-1)" v-if = "$router.currentRoute.path != '/'", data-content="回上一頁", title="回上一頁")
        sui-icon(name='left arrow')
      router-link.item(to='/star', data-content="珍藏", title="珍藏")
        sui-icon(name='star')
      router-link.item(to='/intro')
        sui-icon(name='question', data-content="介紹", title="介紹")
      sui-dropdown.item(icon="save outline", data-content="工具", title="工具")
        sui-dropdown-menu
          sui-dropdown-item(v-show="!iOS()", @click="$router.push('/myapp')")
            | 手機App
          sui-dropdown-item(@click="$router.push('/extension')")
            | 瀏覽器插件
      .right.menu
        router-link.item(to = "/chat", target="_blank")
          i.chat.icon
        a.item(href="https://github.com/g0v/goban", target="_blank")
          i.github.icon
    router-view(:gobans='gobans', @create='create', :chats = "chats", @submit = "submit")
</template>

<script>

import { db, gobansRef, chatsRef } from './firebase/db'

export default {
  name: 'App',
  data () {
    return {
      gobans: undefined,
      chats: undefined
    }
  },
  methods: {
    submit: function (n, email, t) {
      var o = {
        n: n,
        email: email,
        t: t,
        time: (new Date()).getTime()
      }
      if (t) {
        this.$firebaseRefs.chats.push(o)
        window.alert('留言已送出')
      } else {
        window.alert('請輸入留言')
      }
    },
    iOS: function () {
      var ans = false
      var iDevices = [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod'
      ]
      if (navigator.platform) {
        while (iDevices.length) {
          if (navigator.platform === iDevices.pop()) { ans = true }
        }
      }
      // console.log(ans)
      return ans
    },
    create: function (k, o) {
      var obj = o || {id: k}
      obj.id = k
      obj.t = obj.t || k
      obj.hex = obj.hex || 'black'
      obj.tags = obj.tags || [k]
      obj.related = obj.related || [k]
      obj.use_lev = obj.use_lev || true
      db.ref('gobans/' + k).set(obj)
    }
  },
  firebase: {
    gobans: gobansRef,
    chats: chatsRef
  },
  created: function () {
    var vm = this
    vm.$i18n.locale = 'zh-TW'
  },
  watch: {
    $route (to, from) {
      console.log(from.path)
      console.log(to.path)
      this.$gtag.event('action', {
        event_category: 'navigate',
        event_action: 'from:' + from.path + ' to:' + to.path,
        event_label: 'from:' + from.path + ' to:' + to.path,
        value: 'from:' + from.path + ' to:' + to.path
      })
    }
  }
}

</script>

<style>

body {
  background-color: hsla(120, 30%, 30%, 1) !important;
}

.ui.menu {
  background-color: hsla(30, 55%, 33%, 1) !important;
  border: none !important;
}

.ui.menu .item {
  color: white !important;
}

.router-link-exact-active {
  background-color: hsla(30, 55%, 16%, 1) !important;
}

.ui.header {
  color: white !important;
}

h3.sub.header {
  color: yellow !important;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif,
               'Microsoft JhengHei',
                'Microsoft YaHei',
                'STHeiti',
                'Apple LiGothic Medium';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white !important;
  margin-top: 60px;
}

a {
    color: white !important;
    text-decoration: none;
}

a.r {
  margin: 0 1em !important;
}

.ui.segment {
  background-color: hsla(30, 55%, 33%, 1) !important;
}

.yellow {
  color: yellow !important;
}
.gray {
  color: #ccc !important;
}

a, button {
  cursor:pointer;
}

@media screen and (max-width: 420px) {
  .fat-only {
    display: none !important;
  }

  p {
    overflow-x: scroll !important;
  }
}

@media screen and (min-width: 420px) {
  .thin-only {
    display: none !important;
  }
  #app {
    margin-top: 100px;
  }
  #navbar {
    height: 74px;
  }
}
</style>



